.twitter-share-button span {
  vertical-align: top;
}

.container h1 {
  margin-top: 40px;
}

.container h1 + p {
  color: #ababab;
  font-size: 22px;
}

p.description {
  margin-top: 10px;
}

p.numbers {
  font-size: 16px;
  color: #4b4b4b;
}

@media (max-width: 767px) {
  .desc-wrapper-with-media-query {
    margin-top: 20px;
  }
}

div.images-in-columns div:not(.container-width-box) {
  margin-bottom: 15px;
  font-size: 14px;
}

pre {
  padding: 1.5rem;
  margin-right: 0;
  margin-left: 0;
  border-width: .2rem;
  margin: 1rem -15px 0;
  border: solid #f8f9fa;
}

pre p {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
}

.logo {
  display: inline-block;
  width: auto;
  margin-left: 5px;
  position: relative;
  margin-top: -8px;
  height: 47px;
}

.table {
  margin: 30px auto 30px auto;
  max-width: 850px;
}

.table thead {
  text-align: left;
}

.table thead th {
  vertical-align: middle;
  font-size: 16px;
}

.table tbody td {
  font-size: 18px;
}

.container-width-box {
  background: #222;
  color: #fff;
  border-top: 1px solid #6d6d6d;
  border-bottom: 1px solid #6d6d6d;
  text-align: center;
  padding: 4px 0;
}

.device-pixel-ratio {
  background: #222;
  color: #fff;
  border: 1px solid #6d6d6d;
  border-radius: 4px;
  padding: 4px 8px;
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 100;
  max-width: 250px;
}

.device-pixel-ratio span {
  background: #fff;
  display: inline-block;
  border-radius: 4px;
  padding: 0 4px;
  min-width: 30px;
  height: 30px;
  line-height: 30px;
  color: #222;
  text-align: center;
  vertical-align: middle;
}

.device-pixel-ratio .window-width-box {
  display: inline-block;
  margin-left: 4px;
}

.device-pixel-ratio hr {
  margin: 0;
