  font-size: 16px;
  text-align: left;
}

.ready-to-start.filerobot-ui-family h5 {
  font-weight: lighter;
}

.ready-to-start.filerobot-ui-family ul li {
  display: inline-block;
  margin-right: 30px;
}

@media (max-width: 767px) {
  .filerobot-ui-family-label, .filerobot-ui-family-libs {
    min-width: 100% !important;
  }
